.c-sid-default {
  /*
   * If the slider or anything else is covered by the background image,
   * use relative or absolute position on it
   */
  /* Limit the background image by adding overflow: hidden */ }
  .c-sid-default__left-col {
    float: left;
    width: 30%;
    margin-right: 15px; }
  .c-sid-default__left-col img {
    max-height: 250px; }
  .c-sid-default__right-col {
    overflow: hidden; }
  .c-sid-default .filedrag {
    font-weight: bold;
    text-align: center;
    padding: 9% 0;
    color: #ccc;
    border: 2px dashed #ccc;
    border-radius: 7px;
    cursor: default; }
  .c-sid-default .filedrag.hover {
    color: #333;
    border-color: #333;
    background-color: #f9f9f9; }
  .c-sid-default .cropit-image-background {
    opacity: .2; }
  .c-sid-default__size-info {
    margin-top: 5px;
    font-size: 13px; }
  .c-sid-default__delete-image {
    margin-left: -17px; }
  .c-sid-default input.cropit-image-zoom-input {
    position: relative; }
  .c-sid-default #image-cropper {
    overflow: hidden; }
  .c-sid-default__modal .btn {
    position: relative; }
